<template>

    <el-row >
        <el-col :span="2">
            <el-tooltip   placement="top">
                <el-button>应用依赖关系图</el-button>
            </el-tooltip>
        </el-col>
        <el-col :span="24">
            <div  id="mynetwork" style="width:100%; height:800px;background:#F2F6FC;padding:5px">

            </div>
        </el-col>
    </el-row>
</template>

<script scoped>
    import vis from 'vis/dist/vis.js'
    export default {
      name: 'appManage',
      data () {
        return {
          nodes: [],
          edges: []
        }
      },
      methods: {
        getNodesData() {
          this.axios.get('/depend/dispose').then((result) => {
            this.nodes = result.data.nodes
            this.edges = result.data.edges
            // 创建节点数组
            var nodes = new vis.DataSet(this.nodes)
            // 创建关系数组
            var edges = new vis.DataSet(this.edges)
            // 创建一个网络
            var container = document.getElementById('mynetwork')
            var data = {
              nodes: nodes,
              edges: edges
            }
            var options = {
              arrows: 'to',
              nodes: {
                shape: 'dot',
                size: 16
              },
              physics: {
                forceAtlas2Based: {
                  gravitationalConstant: -26,
                  centralGravity: 0.005,
                  springLength: 230,
                  springConstant: 0.18
                },
                maxVelocity: 146,
                solver: 'forceAtlas2Based',
                timestep: 0.35,
                stabilization: {iterations: 150}
              }
            }
            var network = new vis.Network(container, data, options)
            console.log(network)
          })
        }
      },
      mounted () {
        this.getNodesData()
      },
      computed: {

      }
    }
</script>

<style scoped>
    @import 'vis/dist/vis.min.css'
</style>


